.community {
  padding: 78px 0;
  /* background-color: #000000e1; */
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../images/comunity.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 100%;
}

.titleCommunity {
  color: #ffffff;
  margin-bottom: 24px;
}

.paragraph {
  font-size: 18px;
  line-height: 24px;
  color: #ffffff;
  margin-bottom: 24px;
}

.listsContainer {
  display: flex;
  margin-bottom: 40px;
}

.leftList {
  margin-left: 24px;
  margin-right: 65px;
}

.item {
  font-weight: 300;
  font-size: 18px;
  line-height: 24px;
  color: #d7d6d6;
}

.item + .item {
  margin-top: 12px;
}

.downloadBtn {
  padding: 0;
  background-color: transparent;
  border: none;
}

.appStore {
  margin-right: 24px;
}
